import React, { useState, useEffect } from "react";
import ProjectsData from "./ProjectsData";
import ProjectsCondensed from "./ProjectsCondensed";
import ProjectsExpanded from "./ProjectsExpanded";
import 'aos/dist/aos.css'; 
import AOS from 'aos'; 

function Projects() {
  const [selectedProjectKey, setSelected] = useState(null);

  useEffect(() => {
    AOS.init(); 
    AOS.refresh();
  }, []);

  const handleExpand = (key) => {
    setSelected(key);
    document.body.classList.add("modal-open");
  };

  const handleClose = () => {
    setSelected(null);
    document.body.classList.remove("modal-open");
  };

  return (
    <section id="software-projects">
      <h2 data-aos="fade-up" data-aos-duration="1000">Crafted with Code and Curiosity</h2>
      <div className="container projects px-0">
        <div className="row">
          {ProjectsData.map((element) => (
            <div 
              key={element.key} 
              className="col-sm-12 col-md-6 col-lg-4 mb-4"
              data-aos="fade-up-right"
              data-aos-duration="1000"
            >
              <ProjectsCondensed
                id={element.key}
                name={element.name}
                path={element.path}
                tagline={element.tagline}
                techStack={element.techStack}
                iconURL={element.iconURL}
                onClick={handleExpand}
              />
            </div>
          ))}
        </div>

        {selectedProjectKey && (
          <div className="modal-overlay" data-aos="fade-up" data-aos-duration="1000">
            <div className="modal-content">
              <ProjectsExpanded
                project={ProjectsData.find(
                  (p) => p.key === selectedProjectKey
                )} 
                onClose={handleClose} 
              />
            </div>
          </div>
        )}
      </div>
    </section>
  );
}

function getURL(category, desiredType) {
  const link = category.find((object) => object.type === desiredType);
  return link ? link.url : null;
}

export default Projects;